body {
  margin: 0;
}

.wrapper {
  background-color: #add8e6;
  position: fixed;
  left: 0;
  right: 0;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, .1);
}

#search-form {
  width: 300px;
  margin: 0 auto;
  padding: 25px 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 10px;
  margin: 0 0 20px;
  padding-top: 90px;
  display: flex;
}

.photo-card {
  flex-basis: 300px;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, .2);
}

.info {
  justify-content: space-evenly;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.btn-wrapper {
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.load-more {
  cursor: pointer;
  background-color: #add8e6;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

.load-more:active {
  background-color: #87cefa;
}

